
  .movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 100px;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    transition: all .15s linear;
  }

  .movies .movie:hover {
    transform: scale(1.07);
  }

  .movie .movie__link {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    text-decoration: none;
    color: #8a8b92;
    font-weight: 300;
  }

  .movie .movie__link:visited {
    text-decoration: none;
  }
  
  .movie img {
    position: relative;
    top: -60px;
    max-width: 140px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  }
  
  .movie .movie__title,
  .movie .movie__year {
    margin: 0;
    font-weight: 500;
  }
  
  .movie .movie__title {
    height: 50px;
    font-size: 21px;
    color: #2f2f2f;
  }

  .movie .movie__rating {
      font-size: 15px;
      margin: 10px 0;
      font-weight: 400;
  }
  
  .movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
  }
  
  .movie__genres li,
  .movie .movie__year {
    margin-right: 10px;
    font-size: 11px;
  }

  .movie .movie__summary {
      font-size: 15px;
  }